<template>
	<div class="about-container">
		<!-- about member -->
		<section class="about-us-section about-member">
			<div class="about-section-title">
				<h2>운동친구 멤버</h2>
			</div>
			<div class="about-section-contents">
				<div class="card">
					<img class="member-img web" :src="mobileImage.imgPath" />
					<img class="member-img mobile" :src="webImage.imgPath" />
				</div>
			</div>
		</section>
	</div>
</template>

<script>
import { fetchMember } from '@/api/member';

export default {
	created() {
		this.fetchMember();
	},
	data() {
		return {
			webImage: [],
			mobileImage: [],
		};
	},
	methods: {
		goto(url) {
			this.$router.push({ path: url });
		},
		async fetchMember() {
			const { data } = await fetchMember();
			this.webImage = data.filter(item => item.webMobile == 'WEB');
			this.mobileImage = data.filter(item => item.webMobile == 'MOBILE');
			this.webImage = this.webImage[0];
			this.mobileImage = this.mobileImage[0];
		},
	},
};
</script>

<style>
@import '../../../assets/styles/about.css';

@media screen and (max-width: 600px) {
	.member-img.mobile {
		display: block;
	}
	.member-img.web {
		display: none;
	}
}

@media screen and (min-width: 601px) {
	.member-img.mobile {
		display: none;
	}
	.member-img.web {
		display: block;
	}
}
</style>
